import { setup, assign, fromCallback } from 'xstate';

export const timerMachine = setup({
  types: {
    context: {} as {
      duration: number;
      timeLeft: number;
    },
    events: {} as
      | { type: 'TICK' }
      | { type: 'PAUSE' } // Vn cn Pause nu ngi dng mun ngh gia chng
      | { type: 'START' }
      | { type: 'RESUME' }
      | { type: 'STOP_FOR_EDIT' } // S kin khi nhn Edit/Delete
      | { type: 'SELECT_PHASE'; duration: number;  } // S kin chn/auto-next phase
      | { type: 'SELECT_CYCLE'; duration: number; }, // S kin chn cycle mi
    input: {} as { duration: number; }
  },

  actors: {
    intervalTicker: fromCallback(({ sendBack }) => {
      const id = setInterval(() => sendBack({ type: 'TICK' }), 1000);
      return () => clearInterval(id);
    })
  },

  actions: {
    decrementTime: assign({
      timeLeft: ({ context }) => context.timeLeft - 1
    }),
    updateContext: assign({
      duration: ({ event }) => 'duration' in event ? event.duration : 0,
      timeLeft: ({ event }) => 'duration' in event ? event.duration : 0,

    })
  }
}).createMachine({
  id: 'flow-timer',
  initial: 'idle',
  
  context: ({ input }) => ({
    duration: input.duration,
    timeLeft: input.duration,

  }),

  // GLOBAL EVENTS: C th gi t bt k u
  on: {
    // Logic: Khi nhn phase mi (do user chn hoc auto-next), 
    // cp nht context V chuyn ngay sang trng thi 'running'
    SELECT_PHASE: {
      target: '.running', 
      reenter: true, // Reset li timer nu ang chy
      actions: 'updateContext'
    },
    
    // Logic: Khi chn 1 cycle mi, np phase u tin vo nhng KHNG chy
    SELECT_CYCLE: {
        target: '.idle',
        reenter: true, // m bo thot khi cc trng thi khc
        actions: 'updateContext'
    },

    // Logic: Khi nhn Edit/Delete, dng mi th v Idle
    STOP_FOR_EDIT: {
      target: '.idle',
      reenter: true // m bo thot khi running/paused
    }
  },

  states: {
    idle: {
      //  trng thi ny, ch ch SELECT_PHASE  bt u
      // Hoc c th thm nt Start th cng nu mun chy li phase hin ti
      on: {
        START: 'running',
      }
    },

    running: {
      invoke: {
        id: 'ticker',
        src: 'intervalTicker'
      },
      on: {
        TICK: {
          actions: 'decrementTime'
        },
        PAUSE: 'paused',
      },
      // T ng kim tra ht gi
      always: {
        target: 'finished',
        guard: ({ context }) => context.timeLeft <= 0
      }
    },

    paused: {
      on: {
        RESUME: 'running',
        // SELECT_PHASE  Global s t ng x l vic chn phase khc khi ang pause
      }
    },

    finished: {

    }
  }
});